/* Global buttons css
-------------------------------------------------------------- */
/**
 * Buttons
 *
 * The Button `.btn` class can be applied to `<button>`, `<input>`, or `<a>` elements.
 *
 * @section Buttons
 * @page components
 *
 * @example
 * <button class="button">Button</button>
 * <input type="submit" class="btn" value="Input"/>
 * <a href="#" class="btn">Anchor</a>
 */
$buttons-list: 'button',
'input[type="button"]',
'input[type="reset"]',
'input[type="submit"]';

$all-buttons:        assign-inputs($buttons-list);
$all-buttons-active: assign-inputs($buttons-list, active);
$all-buttons-focus:  assign-inputs($buttons-list, focus);
$all-buttons-hover:  assign-inputs($buttons-list, hover);

$all-button-inputs:        $all-buttons;
$all-button-inputs-active: $all-buttons-active;
$all-button-inputs-focus:  $all-buttons-focus;
$all-button-inputs-hover:  $all-buttons-hover;

#{$all-button-inputs},
button,
.button {
	display: inline-block;
	border: 0;
	border-radius: 0;//keep it no radius for ios reset
	padding: $half-base-spacing;
	font-size: $base-font-size;
	color: $lightcolor;
	background: $maincolor;
	transition: background .25s ease-in-out, transform .25s ease-in-out;
	svg {
		pointer-events: none;
	}
}

#{$all-button-inputs-hover},
button:hover,
.button:hover { // Target the :hover pseudo-class
	color: $lightcolor;
	background: darken($maincolor, 5%);
	transform: translateY(-1px);
}

#{$all-button-inputs-focus},
button:focus,
.button:focus { // Target the :focus pseudo-class
	color: $lightcolor;
	background: darken($maincolor, 5%);
	transform: translateY(-1px);
}

#{$all-button-inputs-active},
button:active,
.button:active { // Target the :active pseudo-class
	color: $lightcolor;
	background: $maincolor;
	transform: translateY(1px);
}

.button__icon {
	width: $base-spacing*1.25;
	padding-right: $half-base-spacing;
	vertical-align: -20%;
}
.button--circle {
	border-radius: 50%;
	line-height: 1;
	.button__icon {
		padding-right: 0;
	}
}